<template>
  <div class="fullpage-demo">
    <div :class="['heder-demo', {'header-active': showHeader}]">头部导航部分</div>
    <!--  @onLeave="onLeave" @after-load="afterLoad" -->
    <full-page :options="options" id="fullpage" ref="fullpage">
      <div class="section">
        <h3>fullpage.js</h3>
        <router-link to="/" tag="p">返回首页</router-link>
      </div>
      <div class="section">
        <div class="slide">
          <h3>Slide 2.1</h3>
        </div>
        <div class="slide">
          <h3>Slide 2.2</h3>
        </div>
        <div class="slide">
          <h3>Slide 2.3</h3>
        </div>
      </div>
      <div class="section">
        <h3>Section 3</h3>
      </div>
    </full-page>
  </div>
</template>

<script>
export default {
  name: 'FullPageIndex',
  data () {
    return {
      showHeader: false,
      options: {
        afterLoad: this.afterLoad,
        onLeave: this.onLeave,
        afterRender: function () {
          // window.alert('The resulting DOM structure is ready')
        },
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        navigation: true,
        navigationPosition: 'right',
        anchors: ['page1', 'page2', 'page3'],
        sectionsColor: [
          '#41b883',
          '#ff5f45',
          '#0798ec',
          '#fec401',
          '#1bcee6',
          '#ee1a59',
          '#2c3e4f',
          '#ba5be9',
          '#b4b8ab'
        ],
        loopBottom: true,
        // navigationTooltips导航小圆点的提示，注意按顺序设置
        navigationTooltips: ['page1', 'page2', 'page3', 'page4', 'page5'],
        // showActiveTooltip是否显示当前页面的tooltip信息，默认为false
        showActiveTooltip: true,
        // slidesNavigation 是否显示横向幻灯片的导航，默认为false
        slidesNavigation: true,
        // slidesNavPosition横向导航的位置，默认为bottom，可以设置为top或bottom
        slidesNavPositon: 'top',
        // scrollOverflow内容超过满屏后是否显示滚动条，默认为false,如果为true则会显示滚动条，若需滚动查看内容还需要jquery.slimscroll插件的配合

        // sectionSelector:section选择器。默认为.section

        // slideSelector:slide选择器，默认为.slide
        slideSelector: '.slide',

        controlArrows: true,
        scrollBar: true,
      }
    }
  },
  methods: {
    afterLoad: function (origin, destination, direction) {
      console.log('load', 'After load....', origin, destination)
      if (destination.anchor === 'page1') {
        this.showHeader = false
      } else {
        this.showHeader = true
      }
    },
    onLeave: function (origin, destination, direction) {
      console.log('leave', origin, destination)
      this.showHeader = false
    },
    buildFullPage () {
      // :skip-init="true" 结合这个属性使用
      this.$refs.fullpage.init()
    },
    destroyFullPage () {
      this.$refs.fullpage.destroy()
    }
  },
  activated () {
    // this.buildFullPage()
    console.log('缓存的组件被激活了')
  },
  deactivated () {
    // 多试几次，slide 下边的点数量出 bug 了

    // this.destroyFullPage()
    console.log('缓存的组件失去激活，休眠')
  },
}
</script>

<style lang="scss" scoped src="./demo.scss"></style>
<style lang="scss" scoped>
  .heder-demo {
    width: 800px;
    height: 40px;
    background-color: rgba(92, 240, 216, 0.65);
    position: fixed;
    top: -40px;
    left: 30px;
    z-index: 9;
    transition: top 0.3s cubic-bezier(0.85, -0.01, 0.32, 1.43);
  }

  .header-active {
    top: 5px;
  }
</style>
